        <input class="font_14 login_input" type="text" placeholder="楼盘地址" v-model="address"/>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">楼盘名称</span>
        <input class="font_14 login_input" type="text" placeholder="请输入楼盘名称" v-model="title"/>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">面积</span>
        <input class="font_14 login_input" style="width: 40%;" type="number" placeholder="请输入房屋面积" v-model="metre" @keyup="aaaa"/>
        <span class="float_right">㎡</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">楼层</span>
        <input class="font_14 login_input" type="text" style="width: 20%;" placeholder="所在层数" v-model="louceng[0]"/>
        <span style="color: #000">/</span>
        <input class="font_14 login_input0" type="text" style="width: 20%;" placeholder="总层数" v-model="louceng[1]"/>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">装修情况</span>
        <select class="font_14 select-bar" v-model="zhuangxiu">
          <option>豪装</option>
          <option>精装</option>
          <option>中装</option>
          <option>简装</option>
          <option>毛坯</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">朝向</span>
        <select class="font_14 select-bar" v-model="chaoxiang">
          <option>东</option>
          <option>南</option>
          <option>西</option>
          <option>北</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">区域</span>
        <select class="font_14 select-bar" v-model="area">
          <option>市区</option>
          <option>城东</option>
          <option>城南</option>
          <option>城西</option>
          <option>城北</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_top login_bottom">
        <span class="font_14" style="padding-right: 10px;">户型</span>
        <select class="font_14 select-bar1" v-model="huxing[0]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">室</span>
        <select class="font_14 select-bar1" v-model="huxing[1]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">厅</span>
        <select class="font_14 select-bar1" v-model="huxing[2]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">卫</span>
        <select class="font_14 select-bar1" v-model="huxing[3]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">厨</span>
        <select class="font_14 select-bar1" v-model="huxing[4]">
          <option>0</option>
          <option>1</option>
          <option>2</option>
          <option>3</option>
          <option>4</option>
          <option>5</option>
          <option>6</option>
          <option>7</option>
          <option>8</option>
          <option>9</option>
        </select>
        <span class="font_12 color_8b8b8b">阳台</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">证件</span>
        <select class="font_14 select-bar" v-model="zhengjian">
          <option>两证齐全</option>
          <option>房产证</option>
          <option>土地证</option>
          <option>无证</option>
        </select>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">售价</span>
        <input class="font_14 login_input" style="width: 40%;" type="number" placeholder="请输入购房价格" v-model="price" @keyup="bbbb"/>
        <span class="float_right">万元</span>
      </div>

      <div class="login_bar background_color_ffffff login_bottom login_top">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">单价</span>
        <input class="font_14 login_input" style="width: 40%;" type="number" placeholder="请输入每平方米的单价" v-model="price1" @keyup="cccc"/>
        <span class="float_right">元</span>
      </div>
      <div class="login_bar background_color_ffffff login_bottom">
        <span class="font_14" style="display: inline-block;width: 20%;padding-right: 10px;">房源介绍</span>
        <textarea class="font_14 login_input1" style="width: 100%" type="text" placeholder="请输入房源介绍" v-model="content"/>
      </div>
      <div class="login_bar background_color_ffffff login_top">
        <img :src="item" class="img_service" v-for="(item,index) in imgList" @click="choice(index)"/>
        <img src="static/img/icon_tjtp_tjps.png" class="img_service" @click="uploadImg"/>
      </div>
    </div>
	</div>
</template>
<script>
	export default {
		name: 'addSellers',
		data() {
			return {
        userId:localStorage.getItem('userId'),
        uname:'',
        tell:'',
        metre:'',
        price:'',
        price1:'',
        address:'',
        title:'',
        content:'',
        type:2,
        sameimg:'',
        imgList:[],
        louceng:[],
        zhuangxiu:'豪装',
        chaoxiang:'东',
        area:'市区',
        zhengjian:'两证齐全',
        huxing:['0','0','0','0','0']
			}
		},
    watch : {
      // metre() {
      //   if(this.metre>0){
      //     this.price1 = (this.price*10000/this.metre).toFixed(0)
      //   }else{
      //     this.$toast({
      //       message: '请输入大于0的面积!',
      //       position: "bottom"
      //     });
      //   }
      // },
      // price() {
      //   if(this.price>0&&this.metre>0){
      //     this.price1 = (this.price*10000/this.metre).toFixed(0)
      //   }else{
      //     this.$toast({
      //       message: '请输入大于0的总价!',
      //       position: "bottom"
      //     });
      //   }
      // },
    },
		methods:{
      aaaa(){
          if(this.metre>0){
            if(this.price>0){
              this.price1 = (this.price*10000/this.metre).toFixed(2)
            }
          }else{
            this.$toast({
              message: '请输入大于0的面积!',
              position: "bottom"
            });
          }
      },
      bbbb(){
        if(this.price>0&&this.metre>0){
          this.price1 = (this.price*10000/this.metre).toFixed(2)
        }
      },
      cccc(){
        if(this.price1>0&&this.metre>0){
          this.price = (this.price1/10000*this.metre).toFixed(2)
        }
      },
      addCheck(){
        if(!this.uname||!this.tell||!this.metre||!this.price||!this.price1||!this.address||!this.content||!this.louceng[0]||!this.louceng[1]){
          this.$toast({
            message: '请将内容填写完整!',
            position: "bottom"
          });
        }else if(this.metre<=0||this.price<=0||this.price1<=0){
          this.$toast({
            message: '请输入大于0的房屋总价,房屋面积或房屋单价!',
            position: "bottom"
          });
        }else if(this.imgList.length<1){
          this.$toast({
            message: '至少上传1张图片!',
            position: "bottom"
          });
        }else{
          this.addSellers();
        }
      },
      // choice(index){
      //   this.$dialog.confirm({
      //     message: '请选择切换或删除该张图片',
      //     confirmButtonText:'删除',
      //     cancelButtonText:'切换'
      //   }).then(() => {
      //     let newImgList = [];
      //     for(let i=0;i<this.imgList.length;i++){
      //       if(i!=index){
      //         newImgList.push(this.imgList[i]);
      //       }
      //     }
      //     this.imgList = newImgList;
      //   }).catch(() => {
      //     this.changeImg(index)
      //   });
      // },
      // changeImg(dataMap){
      //   let $this = this;
      //   dsBridge.call('uploadImg');
      //   dsBridge.register('addImgCallback', function(rpdata) {
      //     let imgList1 = [];
      //     for(let i =0;i<$this.imgList.length;i++){
      //       if(i==dataMap){
      //         imgList1.push(rpdata)
      //       }else{
      //         imgList1.push($this.imgList[i])
      //       }
      //     }
      //     $this.imgList = imgList1;
      //   })
      // },
      // uploadImg(){
      //   let $this = this;
      //   if(this.imgList.length==9){
      //     $this.$toast({
      //       message: '最多可以上传9张图片!',
      //       position: "bottom"
      //     });
      //   }else if(this.imgList.length<9){
      //     dsBridge.call('uploadImg');
      //     dsBridge.register('addImgCallback', function(rpdata) {
      //       $this.imgList.push(rpdata);
      //     })
      //   }
      // },
      choice(index){
        this.$dialog.confirm({
          message: '请选择切换或删除该张图片',
          confirmButtonText:'删除',
          cancelButtonText:'切换'
        }).then(() => {
          let newImgList = [];
          for(let i=0;i<this.imgList.length;i++){
            if(i!=index){
              newImgList.push(this.imgList[i]);
            }
          }
          this.imgList = newImgList;
        }).catch(() => {
          this.changeImg(index)
        });
      },
      changeImg(dataMap){
        let $this = this;
        dsBridge.call('uploadImg');
        dsBridge.register('addImgCallback', function(rpdata) {
          if(rpdata.split(',').length>1){
            $this.$toast({
              message: '请上传一张图片',
              position: "bottom"
            });
          }else{
            let imgList1 = [];
            for(let i =0;i<$this.imgList.length;i++){
              if(i==dataMap){
                imgList1.push(rpdata)
              }else{
                imgList1.push($this.imgList[i])
              }
            }
            $this.imgList = imgList1;
          }
        })
      },
      uploadImg(){
        let $this = this;
        if(this.imgList.length==9){
          $this.$toast({
            message: '最多可以上传9张图片!',
            position: "bottom"
          });
        }else if(this.imgList.length<9){
          dsBridge.call('uploadImg');
          dsBridge.register('addImgCallback', function(rpdata) {
            if((rpdata.split(',').length+$this.imgList.length)>9){
              $this.$toast({
                message: '最多可以上传9张图片!',
                position: "bottom"
              });
            }else{
              for(let i=0;i<rpdata.split(',').length;i++){
                $this.imgList.push(rpdata.split(',')[i]);
              }
            }
          })
        }
      },
      addSellers(){
        this.sameimg = this.imgList.join(',');
        let $this = this;
        let obj = {
          userid: $this.userId,
          uname: $this.uname,
          tell: $this.tell,
          metre: $this.metre,
          price: $this.price+','+$this.price1,
          address: $this.address,
          title: $this.title,
          type: $this.type,
          content: $this.content,
          sameimg: $this.sameimg,
          louceng: $this.louceng.join('/'),
          zhuangxiu: $this.zhuangxiu,
          chaoxiang: $this.chaoxiang,
          area: $this.area,
          zhengjian: $this.zhengjian,
          huxing: $this.huxing.join(','),
        };
        let qs = require('qs');
        $this.$ajax.post("samecity/addsamecity", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            $this.$toast({
              message: '发布成功!',
              position: "bottom"
            });
            $this.$router.back(-1);
          }else{
            $this.$toast({
              message: rpdata.data.meta.msg,
              position: "bottom"
            });
          }
        })
        .catch(function(err) {})
      },
		}
	}
</script>
<style scoped>
  .login_input0{
    border: 0;
  }
  .select-bar1{
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    border: 0;
    font-size: 12px;
  }
  .select-bar{
    width: 100px;
    background-color: #f4f4f4;
    padding: 5px;
    border-radius: 5px;
    color: #8a8a8a;
    margin-left: 8%;
    border: 0;
  }
  .img_service{
    width: 32.3%;
  }
  .login_bottom{
    border-bottom: 1px solid #eeeeee;
  }
  .login_input1{
    margin-left: 0%;
    border: 0;
  }
  .login_input{
    margin-left: 8%;
    border: 0;
  }
  .width_5{
    width: 5%;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
  .login_box{
    margin-top: 50px;
  }
</style>



